import React from 'react'
import Link from 'gatsby-link'
import App from 'grommet/components/App'
import Image from 'grommet/components/Image'
import Box from 'grommet/components/Box'
import Hero from 'grommet/components/Hero'
import Card from 'grommet/components/Card'
import Heading from 'grommet/components/Heading'
import Headline from 'grommet/components/Headline'
import Paragraph from 'grommet/components/Paragraph'
import Section from 'grommet/components/Section'
import Carousel from 'grommet/components/Carousel'



//import Person from "../img/person.jpg"

const IndexPage = () => (
  <Box margin={{horizontal: "none", vertical: "large"}} pad={{horizontal: "medium", vertical: "none"}}>
    <Box margin={{horizontal: "none", top: "large"}}>
      <Carousel infinite={true} autoplay={true}>
        <Box align='start'>
          <Heading tag='h1'>
            <a href="https://twitter.com/kainio">@kainio</a> est<br/>
            Soufiane El kassib
          </Heading>
          <Paragraph size="large">
            Concepteur, développeur web et créateur des solutions.
          </Paragraph>
        </Box>
        <Box align='start'>
          <Heading tag='h1'>
            La conception comme service
          </Heading>
          <Paragraph size="large">
            E-commerce, publication, espace de discussion.
          </Paragraph>
        </Box>
        <Box align='start'>
          <Heading tag='h1'>
            <a href="https://twitter.com/creatima">@creatima</a> est un guide d'apprentissage du développement web avec ruby on rails
          </Heading>
          <Paragraph size="large">
            Pour ceux qui veulent créer un Minimum Viable Product pour leur idée innovante.
          </Paragraph>
        </Box>
      </Carousel>
    </Box>

    <Box margin={{horizontal: "none", vertical: "large"}} 
         direction="row">

      <Box pad="small">
        <Section id="about">
          <Heading tag="h3">
            A propos 
          </Heading>
          <Paragraph>
            Mon nom est Soufiane El kassib, j'habite à Salé au Maroc, je travaille sur des solutions techniques qui concernent la vie sociale, que ça soit une platforme commercial locale ou un système de publication pour les organisations, je propose des solutions optimales pour des problèmes bien posés.
          </Paragraph>
        </Section>
      </Box>

      <Box pad="small">
        <Section>
          <Heading tag="h3">
            Technologies et méthodologie de travail
          </Heading>
          <Paragraph>
            Je code en langages Ruby (ruby on rails), PHP (drupal), JavaScript moderne (React, nodejs), je peux présenter les données suivant plusieurs formats comme HTML, CSS, JSON, je manipule les données venant des divers base de données relationnels comme Mysql, sqlite et postgresql, tout cela suivant une méthodologie agile où les modifications sont fréquents lors du processus de développement.
          </Paragraph>
        </Section>
        
        <Section>
          <Heading tag="h3">
            Centres d'intérêt
          </Heading>
          <Paragraph>
            Je suis intéréssé par les sujets liés au développement web ainsi que sur la gestion des projets et la conception des expériences utilisateurs.
          </Paragraph>
        </Section>
        
        <Section id="work">
          <Heading tag="h3">
            Innover dans l'e-commerce et la publication
          </Heading>
          <Paragraph>
            Je propose des solutions conernant le domaine commercial et la publication, dans le but de permettre au commerce local de s'étendre sur internet, aux organisations de publier un contenu ciblé sur un site web responsive facilement avec drupal, un système de gestion de contenu flexible qui s'adapte à leurs besoins, et aussi aux utilisateurs de parler entre eux sur divers sujets.
          </Paragraph>
        </Section>
        
        <Section>
          <Heading tag="h3">
            Lancement de créatima
          </Heading>
          <Paragraph>
            Prochainement, je compte publier sur <a href="http://twitter.com/creatima">@creatima</a> un guide sur le développement web avec le framework ruby on rails qui résumera mon parcours d'apprentissage de cet outil de conception orienté vers le développement des applications web avec le langage ruby, dans le lequel je vais documenter en détails et en illustrations le processus de creation d'un prototype qui servira d'un Mininmum Viable Product (MVP) d'une application libre de messagerie, et qui va être par la suite finalisée puis déployée en ligne sur un serveur.
          </Paragraph>
        </Section>
      </Box>
    </Box>

    <Box>
      <Paragraph>
        Vous pouvez me suivre sur les médias sociaux <a href="https://twitter.com/kainio">twitter</a>, <a href="https://github.com/kainio">github</a>, <a href="https://mastodon.social/@soufiane">mastodon</a>
      </Paragraph>
    </Box>
  </Box>
)

export default IndexPage



